.page{
    width: 100%;
    height: 100%;
    position: absolute;
    background-image: linear-gradient(to right,#1D1A39, #451952, #662549);
}
.top-tags{
    bottom: auto;
    top: 35px
}
.tags{
    color:#CCD5AE;
    opacity: 0.6;
    position: absolute;
    bottom: 0;
    left: 120px;
    font-size: 18px;
    font-family:'la belle Aurore',cursive;
    

}
.bottom-tag-html{
    margin-left: -20px;
    
}
.container{
    width: 100%;
    will-change: contents;
    height: 90%;
    min-height:566px;
    position: absolute;
    opacity: 0;
    top: 5%;
    margin: 0 auto;
    z-index: 1;
    transform-style: preserve-3d;
    animation:fadeIn 1.6s forwards ;
    animation-delay:1.3s ;

}
.about-page{
    .text-zone{
        position:absolute;
        left:10%;
        top:50%;
        transform: translateY(-50%);
        width: 35%;
        vertical-align: middle;
        display: table-cell;
        max-height: 90%;
        h1{
            font-size: 53px;
            font-family: 'Coolvetica';
            font-weight: 400;
            margin-top: 0;
            position: relative;
            margin-bottom: 40px;
            left: 10px;
            &::before{
                content: '<h1>';
                color: #DCB6B1;
                font-family: 'La Belle Aurore';
                position: absolute;
                font-size: 18px;
                margin-top: -10px;
                left: -10px;
                opacity: 0.6 ;
                line-height: 18px;

            } 
            &::after{
                content: '</h1>';
                color: #DCB6B1;
                font-family: 'La Belle Aurore';
                font-size: 18px;
                position: absolute;
                left: -30px;
                bottom: -20px;
                margin-left: 20px;
                opacity: 0.6;
            }

        }
        p{
            font-size: 13.5px;
            color: #fff;
            font-family: sans-serif;
            font-weight: 300;
            min-width: fit-content;
            animation: pulse 1.3s;

            &:nth-of-type(1){
                animation-delay: 1.1s;
            }
            &:nth-of-type(2){
                animation-delay: 1.3s;
            }
            &:nth-of-type(3){
                animation-delay: 1.5s;
            }
        }


    }
    .text-animate-hover{
        &:hover{
        color: #CCD5AE;
        } 
    }

}