.home-page{
    .text-zone{
        position: absolute;
        left: 10%;
        top: 50%;
        transform: translateY(-50%);
        width: 40%;
        max-height:90% ;
    }
    h1{
        color: #ffffff;
        font-size: 56px;
        margin: 0;
        font-family: 'Coolvetica';
        font-weight: 400;
        &::before{
            content: '<h1>';
            font-family: 'La Belle Aurore';
            color: #DCB6B1;
            font-size: 18px;
            position: absolute;
            margin-top: -40px;
            left: -15px;
            opacity: 0.6;

        }
        &::after{
            content:'</h1>';
            font-family: 'La Belle Aurore';
            color: #DCB6B1;
            font-size: 18px;
            position: absolute;
            margin-top: 37px;
            margin-left: 20px;
            animation: fadeIn  1s 1.7s backwards;
            opacity: 0.6;
            
        }
        img{
            width: 60px;
            margin-left: 25px;
            // margin-right: -0px;
            margin-bottom:-5px;
            opacity: 0;
            height: auto;
            animation: rotateIn 1s linear both;
            animation-delay: 1.4s;
  
        }
    }
    h2{
        color:#F2926F;
        margin-top: 20px;
        font-weight: 400;
        font-family: sans-serif ;
        font-size: 11px;
        letter-spacing: 3px;
        animation: fadeIn 1s 1.8s backwards;
    }
.flat-button{
    color:#BD82BA;
    font-size: 13px;
    font-weight: 400;
    letter-spacing: 4px;
    font-family: sans-serif;
    text-decoration: none;
    padding: 10px 18px;
    border: 1px solid #BD82BA;
    margin-top: 25px;
    float:left;
    animation: fadeIn 1s 1.8s backwards;
    white-space: nowrap;
    &:hover{
        background-color:#F5D6DC;
        border:#F5D6DC;
        color:#BD82BA;

    }




}
}
